body {
  font-family: 'Times New Roman', Times, serif;
  font-size: 18px;
  background-color: #dae7f5;
  
}

#headline { 
  background-color: #5a6cc4;
}

#headline h1 {
  margin-left: 20px;
  font-size: 45px;
  font-family: 'Caveat', cursive;
  font-weight: 400;
}
#resrc {
  padding: 25px;
  font-size: 16px;
  font-family: Tahoma, Geneva, sans-serif;
}

button {
  padding: 16px;
  font-family: Tahoma, Geneva, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid black;
  text-align: right;
  width: 100%;
  background-color: #3d90e3;
  /*color: #F77777; */

}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: #151f75;
  color: white;
}
#projList a:hover {
  /*background: #3d90e3; */
  color: #3d90e3;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
  margin-left: 20px;
  
}

.project-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.project-list-item p {
  padding-bottom: 32px;
  
}

#add-comment-form {
  
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 100px;
  background-color: #58a808;
  
}


#like-section {
  text-align: right;
}

#like-section button {
  width: 80px;
  text-align: center;
  border-radius: 20px;
  background-color: #cfa255;
  display: inline-block;
  font-size: 12px;
}

#like-section button, p {
  display: inline-block; 
  margin-right: 10px;
  
}

#cmtblock {
  border-bottom: 2px solid black;
  display: block;
}

#otherPro {
  border-bottom: 2px solid black;
  display: block;
}

.comment {
  /*border-bottom: 2px solid black;
  display: block;*/
  padding: 16px;
}

.comment h4 {
  text-align: right;
  font-size: 14px;
}

#downloadbn {
  background-color: #58a808;
}

